.sr-only {
  position: absolute;
  width: 1px;
  height: 1px;
  padding: 0;
  margin: -1px;
  overflow: hidden;
  clip: rect(0, 0, 0, 0);
  border: 0; }

.sr-only-focusable:active, .sr-only-focusable:focus {
  position: static;
  width: auto;
  height: auto;
  margin: 0;
  overflow: visible;
  clip: auto; }

main {
  position: relative;
  overflow: hidden; }

#rotator:after {
  content: '';
  z-index: -1;
  width: 55%;
  height: 75%;
  position: absolute;
  left: -34%;
  background: url("../img/src/hero-bg.svg");
  background-size: 100%;
  background-repeat: no-repeat;
  -webkit-animation: spinner 200s infinite;
  animation: spinner 200s infinite; }

@-webkit-keyframes spinner {
  0% {
    transform: rotate(0deg);
    animation-timing-function: linear;
    transform-origin: center center; }
  100% {
    transform: rotate(360deg);
    animation-timing-function: linear;
    transform-origin: center center; } }

@keyframes spinner {
  0% {
    transform: rotate(0deg);
    animation-timing-function: linear;
    transform-origin: center center; }
  100% {
    transform: rotate(360deg);
    animation-timing-function: linear;
    transform-origin: center center; } }

h1 {
  font-size: 3.6em;
  color: #424F6F;
  font-family: libre;
  font-weight: normal; }

p {
  font-size: 16px;
  color: #666666;
  font-family: rubik; }

.btn-primary {
  background: #424F6F;
  color: #FFFFFF;
  padding: 16px 48px;
  font-size: 18px;
  text-decoration: none;
  font-family: rubik;
  border-radius: 0px;
  border: none; }
  .btn-primary:after {
    content: "";
    font-family: fontawesome;
    margin-left: 16px;
    font-size: 13px; }
  .btn-primary:hover {
    background-color: #333d55;
    transition: all 0.3s ease;
    cursor: pointer; }
  .btn-primary:active {
    background: #12161f; }

.btn-secondary {
  background: #FFFFFF;
  color: #6CCDE8;
  padding: 16px 48px;
  font-size: 18px;
  text-decoration: none;
  font-family: rubik;
  border-radius: 0px;
  border: none;
  border: 2px solid #6CCDE8; }
  .btn-secondary:after {
    content: "";
    font-family: fontawesome;
    margin-left: 16px;
    font-size: 13px; }
  .btn-secondary:hover {
    background-color: #ebebeb;
    transition: all 0.3s ease;
    cursor: pointer; }
  .btn-secondary:active {
    background: #bfbfbf; }

.nav--container {
  justify-content: space-between;
  border-top: 1px solid #eeeeee;
  align-items: center; }

.nav--logo {
  justify-content: flex-start;
  padding: 0 32px; }

.nav--items {
  justify-content: flex-end;
  align-items: center; }
  .nav--items-list {
    padding: 32px;
    position: relative; }
    .nav--items-list a {
      font-size: 20px;
      color: #424F6F;
      font-weight: 500;
      text-transform: uppercase; }
      .nav--items-list a.active {
        color: #6CCDE8; }
        .nav--items-list a.active:before {
          content: '';
          width: 100%;
          height: 2px;
          position: absolute;
          top: 0px;
          left: 0px;
          background-color: #6CCDE8; }

.hero {
  text-align: center;
  margin: 64px; }
  .hero__logo {
    position: relative;
    width: 100%;
    min-height: 1px;
    padding-right: 15px;
    padding-left: 15px;
    margin-bottom: 32px; }
    @media (max-width: 32em) {
      .hero__logo {
        flex: 0 0 100%;
        max-width: 100%; } }
    @media (min-width: 32.1em) {
      .hero__logo {
        flex: 0 0 41.66667%;
        max-width: 41.66667%; } }
    .hero__logo img {
      max-width: 100%;
      height: auto; }
  .hero__title {
    position: relative;
    width: 100%;
    min-height: 1px;
    padding-right: 15px;
    padding-left: 15px; }
    @media (max-width: 32em) {
      .hero__title {
        flex: 0 0 100%;
        max-width: 100%; } }
    @media (min-width: 32.1em) {
      .hero__title {
        flex: 0 0 83.33333%;
        max-width: 83.33333%; } }
  .hero__sub-title {
    position: relative;
    width: 100%;
    min-height: 1px;
    padding-right: 15px;
    padding-left: 15px;
    font-size: 14px;
    color: #6CCDE8;
    text-transform: uppercase; }
    @media (max-width: 32em) {
      .hero__sub-title {
        flex: 0 0 100%;
        max-width: 100%; } }
  .hero__hummingbird {
    position: relative;
    width: 100%;
    min-height: 1px;
    padding-right: 15px;
    padding-left: 15px;
    font-size: 25px;
    font-weight: 100;
    line-height: 1.6;
    margin: 32px 0; }
    @media (max-width: 32em) {
      .hero__hummingbird {
        flex: 0 0 100%;
